.about {
	width: 100%;
	min-height: 635px;
	background-image: url('../images/decor/bg/bg6.jpg');
	background-repeat: no-repeat;
	background-size: 100% 100%;
	background-position: center center;
	position: relative;
	padding: rem(20) 0 7rem 0;
	overflow: hidden;
	@include desktop {
		padding: rem(20) 0 9rem 0;
		background-image: url('../images/decor/bg/daisy-left.png'),
		url('../images/decor/bg/daisy_right_bg.png');
		background-color: $about-bg-color;
    	background-size: 125%, 35%;
    	background-position: left 1%, right 1%;
	}
	@include laptop {
		padding: rem(20) 0 8.5rem 0;
		background-image: url('../images/decor/bg/daisy-left.png'),
		url('../images/decor/bg/daisy_right_bg.png');
		background-color: $about-bg-color;
    	background-size: 140%, 38%;
    	background-position: left 1%, right 1%;
	}
	@include tablet {
		padding: rem(20) 0 8.5rem 0;
		background-image: url('../images/decor/bg/daisy_bg.png');
		background-color: $about-bg-color;
    	background-size: contain;
    	background-position: center top;
	}
	@include mobile {
		padding: rem(20) 0 5.5rem 0;
		background-position: center 5%;
	}
}

.zabor-bg {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 0;
}

.zabor-img {
	width: 100%;
	height: auto;
}

.about__card {
	width: 100%;
	min-height: 440px;
	display: flex;
    overflow: hidden;
    @include laptop {
    	min-height: 563px;
    }
    @include tablet {
    	flex-direction: column;
    	align-items: center;
    }
    @include mobile {
    	min-height: 600px;
    }
}

.about__img-wr {
	@include size(rem(361));
	border-radius: 50%;
	flex-shrink: 0;
	margin: 0 auto;
	background-size: cover;
	background-position: center center;
	@include laptop {
		@include size(20rem);
	}
	@include tablet {
		margin: 0 auto rem(30) auto;
		@include size(20.5rem);
	}
}

.about__descrip {
	font-size: rem(22);
	font-weight: 300;
	font-style: normal;
	line-height: 1.425;
	letter-spacing: 1.3px;
	text-align: left;
	color: $primaryColor;
	width: 60%;
	margin-left: auto;
	@include laptop {
		width: 56%;
	}
	@include custom_1800 {
		width: 798px;
	}
	@include custom {
		width: 798px;
	}
	@include tablet {
		width: 100%;
	}
}

.about__text {
	margin-bottom: rem(30);
	@include tablet {
		text-align: center;
	}
}

.about__author {
	margin-bottom: rem(25);
	@include tablet {
		text-align: right;
	}
	@include mobile {
		text-align: center;
	}
}

.about__contacts {
	width: 100%;
	height: 5rem;
	margin-top: rem(10);
	display: flex;
	justify-content: flex-end;
	align-items: center;
	position: relative;
	@include custom_1800 {
    	top: -103px;
	}
	@include custom {
    	top: -103px;
	}
	@include desktop {
		margin-top: -10px;
	}
	@include tablet {
		margin-top: rem(20);
		justify-content: center;
		flex-wrap: wrap;
		height: 9rem;
	}
}

.about__whatsApp, .about__phone {
	max-width: rem(375);
	width: 45%;
	height: rem(55);
	border-radius: rem(27,5);
	margin-right: rem(20);
	overflow: hidden;
	@include tablet {
		width: 100%;
	}
}

.about-phone__link {
	display: flex;
	width: 100%;
	height: 100%;
	color: $text-second-color;
	font-size: rem(20);
	align-items: center;
	font-weight: bold;
	font-style: normal;
	justify-content: center;
	transition: color 0.2s, background-color 0.2s;
	letter-spacing: 1.4px;
	padding: rem(2) rem(11);
	background-color: $primaryColor;
	&:hover, &:active {
		color: rgba(#77b3d4, 0.95);
		background-color: rgba($primaryColor, 0.99);
	}
	&:hover svg, &:active svg{
		fill: currentColor;
	}
	@include custom {
		font-size: rem(24);
	}
	& span {
		margin: 0 auto;
	}
}

.about-whatsApp__link {
	display: flex;
	width: 100%;
	height: 100%;
	color: $text-second-color;
	font-size: rem(20);
	align-items: center;
	font-weight: bold;
	font-style: normal;
	transition: color 0.2s, background-color 0.2s;
	letter-spacing: 1.4px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	padding: rem(2) rem(11);
	background-color: $primaryColor;
	&:hover, &:active {
		color: rgba(#57bb63, 0.95);
		background-color: rgba($primaryColor, 0.99);
	}
	&:hover svg, &:active svg{
		fill: currentColor;
	}
	@include custom {
		font-size: rem(24);
	}
	& span {
		margin: 0 auto;
	}
}

.call-us-svg {
	@include size(rem(40));
	margin-right: rem(10);
	fill: #77b3d4;
	stroke: $primaryColor;
	transition: fill 0.2s;
}

.whatsapp-svg {
	@include size(rem(40));
	margin-right: rem(10);
	fill: #57bb63;
	stroke: $primaryColor;
	transition: fill 0.2s;
}


