.submit-bl {
	width: 100%;
	margin-top: rem(16);
}

.submit-button {
	@include size(100%, rem(56));
	border-radius: rem(10);
	background-color: $orange-color;
	outline: none;
	overflow: hidden;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	border: none;
	transition: background-color 0.2s, opacity 0.15s;
	box-shadow: 2px 13px 9px -11px $orange-dark-color;
	&:hover {
		cursor: pointer;
		background-color: rgba($orange-dark-color, 0.95);
	}
	& span {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		vertical-align: middle;
		font-size: rem(36);
		font-weight: bold;
		font-style: normal;
		line-height: 3rem;
		letter-spacing: 2.2px;
		text-align: center;
		color: $primaryColor;
	}
	&.font-sz-18 {
		@include size(rem(294), rem(52));
		margin: 0 auto;
		box-shadow: 2px 13px 5px -13px $orange-dark-color;
		@include desktop {
			width: 17.6rem;
		}
	}
	&.font-sz-18 span{
		font-size: rem(18);
		font-weight: bold;
		font-style: normal;
		line-height: 2rem;
		letter-spacing: 1.1px;
		text-align: center;
		color: $primaryColor;
	}
	&.bsh-none {
		box-shadow: none;
	}
	&.bsh-none span {
		line-height: 3.1rem;
	}
	&.sm-fsz-1-8rem span {
		@include s-mobile {
			font-size: (1.8rem);
		}
	}
	&:disabled:hover {
		background-color: $orange-color;
		opacity: 0.2;
	}
}


