.calculation {
	width: 100%;
	min-height: 720px;
	display: flex;
	flex-direction: column;
	@include custom {

	}
	@include desktop {

	}
	@include tablet {
		height: initial;

	}
}

.calculation__title {
	width: 100%;
	min-height: rem(98);
	background-color: #f2a000;
	padding: rem(30) 0;
	display: flex;
	align-items: center;
}

.calculation__wrapper {
	width: 100%;
	min-height: 770px;
	position: relative;
	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		overflow: hidden;
		z-index: -1;
		background-image: url('../images/decor/bg/zemlya.jpg');
		background-repeat: no-repeat;
		background-position: bottom center;
		background-size: cover;
	}
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		overflow: hidden;
		z-index: -1;
		background: rgba(0,0,0, 0.4);
	}
	@include tablet {
		min-height: 847px;
		background-position: center center;
		background-size: auto 100%;
	}
	@include s-mobile {
		min-height: 781px;
	}
}
.rulon-decor {
	position: absolute;
	width: 830px;
	z-index: 1;
	left: 50%;
	bottom: 0;
	transform: translate(-50%, 25%);
	@include laptop {
		width: 80%;
	}
	@include tablet {
		width: 80%;
	}
}

.rulon-decor__img {
	max-width: 100%;
	width: 100%;
	height: auto;
}

.calculation__left {
	width: 49.4%;
	height: 100%;
	@include tablet {
		width: 90%;
		padding: rem(10) 0;
	}
}
.promo-card {
	width: 100%;
	height: 100%;
	@include tablet {
		margin-bottom: 1.7rem;
	}
}

.calculation__right {
	width: 47%;
	max-width: rem(520);
	height: 100%;
	@include tablet {
		width: 90%;
		margin-bottom: rem(60);
	}
}

#order-calculation {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: flex-end;
}

.promo-card__content {
	display: flex;
	flex-direction: column;
}

.promo-card__description {
	font-size: rem(18);
	font-weight: bold;
	font-style: normal;
	line-height: 1.425;
	letter-spacing: 1.1px;
	text-align: left;
	color: $primaryColor;
}

.promo-card__item {
	display: flex;
	align-items: flex-start;
	margin-bottom: rem(10);
	&:last-child {
		margin-bottom: 0;
	}
}

.check-svg {
	@include size(rem(40));
	margin-right: rem(10);
	flex-shrink: 0;
}

.promo-card__price {
	color: $primaryColor;
	font-weight: bold;
	font-style: normal;
	text-align: left;
	width: 100%;
}

.promo-card__price-title {
	font-size: rem(30);
	line-height: 2;
	letter-spacing: 1.8px;
	@include tablet {
		font-size: rem(25);
	}
}

.promo-card__price-amount {
	font-size: rem(60);
  	line-height: 1;
  	letter-spacing: 3.6px;
  	@include tablet {
  		font-size: rem(50);
  		line-height: 1.25;
  	}
}


