.service {
	width: 100%;
	min-height: 64rem;
	position: relative;
	padding-top: 7.125rem;
	background: $bgColor;
	@include custom {
		padding-top: 0;
	}
}

.service__list {
	width: 100%;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: baseline;
	margin-bottom: rem(20);
}

.service__item {
	width: 31%;
	//height: 100%;
	height: 50%;
	padding: rem(5);
	overflow: hidden;
	margin-bottom: rem(50);
	@include laptop {
		width: 49%;
		height: 30%;
	}
	@include tablet {
		width: 49%;
		height: 30%;
	}
	@include tablet-610 {
		width: 100%;
		height: 15%;
	}
}

.service__img-wr {
	@include size(16rem);
	border-radius: 50%;
	max-width: 100%;
	overflow: hidden;
	margin-bottom: rem(15);
	background-size: 100% 100%;
	background-position: center center;
	margin: 0 auto rem(17) auto;
	@include custom {
		@include size(19rem);
	}
}

.service__img {
	max-width: 100%;
	height: auto;
}

.service__descrip {
	width: 100%;
	min-height: rem(56);
	font-size: rem(24);
	font-weight: bold;
	font-style: normal;
	line-height: 1.35;
	letter-spacing: 1.4px;
	text-align: center;
	color: $text-second-color;
	margin-bottom: rem(15);
	@include desktop {
		font-size: rem(20.5);
	}
	@include tablet {
		font-size: rem(20.5);
	}
}


