* {
	box-sizing: border-box;
}

body, html {
  	color: $text-color;
  	font-family: $text-font-stack;
	margin: 0;
	padding: 0;
	width: 100%;
	font-weight: 400;
	font-size: 16px;
	line-height: 1.425rem;
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-display: optional;
	position: relative;
	min-height: 100%;
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		display: block;
		background: rgba($bgColor, 0.3);
		z-index: -1;
	}
	@include laptop {
		font-size: 15px;
	}
	@include tablet {
		font-size: 14px;
	}
	@include mobile {
		font-size: 13px;
	}
	@include s-mobile {
		font-size: 12px;
	}
}

*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: $primaryColor;
  text-decoration: none;
}

ul {
	margin: 0;
	padding: 0;
}

ul li {
	list-style: none;
}

h1, h2, h3, p {
	margin: 0;
}

svg {
	fill: currentColor;
	stroke: currentColor;
}

button, input, select, textarea {
  font-family: $text-font-stack;
 }

.wrapper {
	@include size(100%);
	margin: 0;
	position: relative;
	min-height: 100vh;
	@include custom_1800 {
		width: 1800px;
		margin: 0 auto;
	}
	@include custom {
		min-height: 650px;
	}
}

.container {
	min-width: 80%;
	width: 1170px;
	height: 100%;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	position: relative;
	@include custom {
		width: 85%;
	}
	@include desktop {
		width: 95%;
	}
	@include laptop {
		width: 95%;
	}
	@include tablet {
		width: 92%;
	}
	@include s-mobile {
		width: 95%;
	}
	&.container_fxdir-row {
		flex-direction: row;
		justify-content: space-between;
		align-items: baseline;
		padding: rem(20) 0;	
		@include tablet {
			flex-direction: column;
			flex-wrap: wrap;
			align-items: center;
		}	
	}
	&.container_footer {
		min-height: 259px;
		justify-content: stretch;
		flex-direction: row;
		align-items: center;
		@include tablet {
			min-height: 21.5rem;
		}
		@include mobile {
			min-height: 26rem;
		}
		@include s-mobile {
			min-height: 27rem;
		}
	}
	&.w-100 {
		width: 100%;
	}
}


