.form-inner {
	width: 100%;
	&.w-95 {
		width: 95%;
		margin: 0 auto;
		&include laptop {
			width: 100%;
		}
	}
}

.form-row {
	position: relative;
	width: 100%;
	margin-bottom: rem(10);
	&.mb-17 {
		margin-bottom: rem(17);
	}
}

.input {
	width: 100%;
	height: rem(56);
	border-radius: rem(10);
	background-color: rgba($primaryColor, 0.53);
	border: 1px solid transparent;
    padding: rem(10) rem(10) 11px rem(70);
    outline: none;
    text-align: center;
    overflow: hidden;
    transition: border 0.2s;
    font-size: rem(36);
	font-weight: 300;
	font-style: normal;
	letter-spacing: 2.2px;
	@include laptop {
		font-size: rem(20);
		@include placeholder {
			font-size: rem(20);
		}
	}
    @include placeholder {
		font-size: rem(36);
		font-weight: 300;
		font-style: normal;
		letter-spacing: 2.2px;
		text-align: center;
		color: $placeholder-color;
	}
	&:hover, &:focus {
		border: 1px solid $primaryColor;
		cursor: pointer;
	}
	&.bgc-text-color {
		background-color: rgba($text-second-color, 0.53);
		color: $primaryColor;
		box-shadow: 2px 13px 9px -11px $text-second-color;
		padding: rem(10) rem(20) 11px rem(70);
		@include placeholder {
			color: $primaryColor;
		}
	}
	&.bgc-text-color:hover, &.bgc-text-color:focus {
		border: 0.5px solid rgba($text-color, 0.4);
	}
	&.sm-fsz-1-8rem {
		@include laptop {
			@include placeholder {
				font-size: 1.8rem;
			}
		}
		@include tablet {
			@include placeholder {
				font-size: 1.8rem;
			}
		}
	}
}
 
.user-svg {
	position: absolute;
	@include size(2.6rem);
	top: 50%;
	left: rem(10);
	transform: translateY(-50%);
}

.phone-svg {
	position: absolute;
	@include size(2.7rem, 3rem);
	top: 50%;
	left: rem(10);
	transform: translateY(-50%);
}

.size-svg {
	position: absolute;
	@include size(2.8rem);
	top: 50%;
	left: rem(10);
	transform: translateY(-50%);
}


