.hamburg-menu {
	position: absolute;
	top: 0;
	left: 0;
	@include size(rem(78), rem(72));
	touch-action: manipulation;
}

.hamburg-menu__open-link {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	@include size(100%);
	font-family: 'pf_square_sans_pro';
	font-size: rem(24);
	font-weight: normal;
    font-style: normal;
    color: $text-color;
}

.menu-svg {
	@include size(4.5rem, 100%);
	fill: $text-color;
	stroke: $text-color;
}

.fat-nav {
  top: 0;
  left: 0;
  z-index: 9999;
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  transform: scale(0.0001);
  background: rgba(0, 0, 0, 0.65);
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}

.fat-nav__wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fat-nav.active {
	transform: scale(1);
	transition: transform 0.2s;
	will-chanche: transform;
}

.hamburg-menu__list {
	max-width: 35%;
	display: flex;
	flex-direction: column;
	@include mobile {
		max-width: 55%;
	}
}

.hamburg-menu__close {
	position: absolute;
	top: 40px;
	right: 10%;
	@include size(2.8rem, 2.5rem);
}

.close-svg {
	width: 2.8rem;
	height: 2.5rem;
	fill: $primaryColor;
	stroke: $primaryColor;
}

.hamburg-menu__item {
	padding: 0.8rem 0;
	border-bottom: 1px solid $primaryColor;
	text-align: center;
	&:last-child {
		border-bottom: none;
	}
}

.hamburg-menu__link {
	color: $primaryColor;
	font-size: rem(36);
	font-weight: 300;
	text-align: center;
	line-height: 1.3;
	@include tablet {
		font-size: rem(30);
	}
	@include s-mobile {
		font-size: rem(19);
	}
}


