.main-title {
	width: 100%;
	text-align: center;
}
.title {
	color: $primaryColor;
	font-size: rem(36);
	font-weight: bold;
	font-family: 'open_sans';
	font-style: normal;
	letter-spacing: 2.2px;
	text-align: center;
	line-height: 1.2;
	width: 100%;
	vertical-align: middle;
	@include tablet {
		font-size: rem(30);
		line-height: 2.6;
	}
	@include s-mobile {
		font-size: rem(19);
	}
	&.text-color {
		color: $text-second-color;
		margin-bottom: rem(70);
		@include tablet {
			margin-bottom: rem(30);
			line-height: 1.34;
		}
	}
	&.lh-1-34 {
		@include tablet {
			line-height: 1.34;
		}
	}
	&.bg-orange {
		background-color: $orange-color;
		width: 60%;
		line-height: 1.62;
		margin: 0 auto;
		@include custom_1800 {
			width: 50%;
		}
		@include desktop {
			width: 74%;
		}
		@include laptop {
			width: 94%;
		}
		@include tablet {
			width: 84%;
			font-size: rem(28);
		}
		@include mobile {
			width: 80%;
			font-size: rem(25);
		}
		@include s-mobile {
			width: 90%;
			font-size: rem(19);
		}
	}
	&.mb-25 {
		margin-bottom: rem(20);
	}
	&.mb-80 {
		margin-bottom: rem(80);
		@include tablet {
			margin-bottom: rem(30);
		}
	}
}

h1.title.bg-orange {
	@include laptop {
		width: 667px;
	}
	@include tablet {
		width: 650px;
	}
	@include tablet-610 {
		width: 450px;
	}
	@include mobile {
		width: 330px;
	}
	@include s-mobile {
		width: 290px;
	}
}

.card-title {
	font-size: rem(30);
	font-weight: bold;
	line-height: 1.2;
	letter-spacing: 1.8px;
	text-align: left;
	color: $primaryColor;
	margin-bottom: rem(15);
	@include tablet {
		margin: 0 -5px 15px -5px;
		text-align: center;
	}
	&.text-color {
		text-align: center;
		color: $text-second-color;
		width: 100%;
		margin-bottom: rem(20);
		@include s-mobile {
			width: 97%;
			margin: 0 auto rem(20) auto;
		}
	}
	&.bg-orange {
		background-color: $orange-color;
		text-align: left;
		color: $primaryColor;
		line-height: 3.8rem;
		width: 100%;
		margin-bottom: rem(10);
		padding-left: 0.7rem;
    	font-size: 2.1rem;
    	@include tablet {
    		margin: 0 0 rem(10) 0;
    	}
	}		
}


