.footer {
	width: 100%;
	min-height: rem(259);
	background-image: url('../images/decor/bg/footer.jpg');
	overflow: hidden;
	background-repeat: no-repeat;
	background-size: cover;
	@include tablet {
		min-height: 21.5rem;
	}
	@include mobile {
		min-height: 26rem;
	}
	@include s-mobile {
		min-height: 27rem;
	}
}

.footer_wr {
	width: 100%;
	min-height: 100%;
	display: flex;
	@include tablet {
		flex-direction: column;
	}
}

.footer__left {
  font-family: OpenSans;
  font-size: rem(20);
  font-weight: 300;
  font-style: normal;
  letter-spacing: 1.2px;
  text-align: left;
  color: $primaryColor;
  text-shadow: 0px 1px 0 $text-color;
  width: 60%;
  margin-right: auto;
  @include tablet {
  	width: 100%;
  	order: 3;
  	margin: rem(10) auto auto 0;
  }
}

.footer__title {
	font-weight: normal;
	font-size: rem(20);
	font-style: normal;
	letter-spacing: 1.2px;
	text-align: left;
	line-height: 1.4;
	color: $primaryColor;
	text-shadow: 0px 1px 0 $text-color;
	@include tablet {
		text-align: left;
		width: 100%;
	}
}

.footer__text {
	font-size: rem(20);
	font-weight: 300;
	font-style: normal;
	letter-spacing: 1.2px;
	text-align: left;
	color: $primaryColor;
	text-shadow: 0px 1px 0 $text-color;
	@include tablet {
		text-align: left;
		width: 90%;
	}
}

.footer__center {
	width: 0;
	height: rem(115);
	border-left: 1px solid rgba($primaryColor, 0.25);
	margin: 0 auto;
	@include tablet {
		border-bottom: 1px solid rgba($primaryColor, 0.25);
		width: 55%;
		height: 0;
		order: 2;
		margin: auto;
	}
}

.footer__right {
    width: 30%;
    margin-left: auto;
    @include tablet {
    	width: 100%;
    	order: 1;
    	margin: auto 0 rem(10) auto;
    }
}

.contact-links__title {
	font-size: 20px;
	font-weight: bold;
	font-style: normal;
	letter-spacing: 1.2px;
	text-align: left;
	text-shadow: 0px 1px 0 $text-color;
	color: $primaryColor;
	line-height: 1.4;
	@include tablet {
		width: 100%;
		text-align: right;
	}
}

.contact-links__item {
	text-align: left;
	@include tablet {
		text-align: right;
	}
}

.contact-links__link {
	font-size: 20px;
	font-weight: bold;
	font-style: normal;
	letter-spacing: 1.2px;
	text-align: left;
	color: rgb(223, 210, 0);
	text-shadow: 0px 1px 0 $text-color;
	line-height: 1.2;
	transition: color 0.2s;
	&:hover, &:active {
		color: $orange-dark-color;
	}
	&:visited {
		color: $bgColor;
	}
}


