.smeta {
	width: 100%;
	height: rem(445);
	min-height: rem(445);
	background-color: $bgColor;
	@include tablet {
		min-height: 100vh;
	}
}
.smeta__wraper {
	width: 100%;
	height: 100%;
	display: flex;
	@include tablet {
		flex-direction: column;
	}
}
.smeta__map {
	width: 50%;
	height: rem(445);
	background-size: cover;
	background-position: center center;
	position: relative;
	@include tablet {
		width: 100%;
		height: 60vh;
	}
}

.smeta__map-title {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;
	font-size: rem(36);
	font-weight: bold;
	font-style: normal;
	letter-spacing: 2.2px;
	color: $primaryColor;
	width: 90%;
	max-height: 80%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.map-title__row-one {
	width: 100%;
	& span {
		background-color: $orange-dark-color;
		line-height: 1.85;
	}
}

.map-title__row-two {
	width: 100%;
	& span {
		background-color: $orange-color;
		line-height: 1.3;
	}
}

.order-call {
	width: 47%;
	height: 100%;
	max-width: rem(565);
	margin: auto;
	padding: rem(10) 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	@include tablet {
		width: 100%;
		height: 29rem;
	}
}


