.hero {
	padding: rem(40) 0 0;
	width: 100%;
	height: 100vh;
	background: url('../images/decor/bg/header.jpg') center top/cover no-repeat;
	min-height: 630px;
	overflow: hidden;
	@include tablet {
		min-height: 100vh;
	}
}

.arrowdown-link {
	display: block;
	@include size(rem(90));
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	margin-bottom: rem(30);
	transition: transform 0.3s;
	&:hover {
		transform: scale(1.25);
	}
}

.arrowdown-svg {
	@include size(rem(85));
	fill: $primaryColor;
	stroke: $primaryColor;
}


