.gallery {
	width: 100%;
	height: 640px;
	background-image: url('../images/decor/bg/bg4.jpg');
	background-size: cover;
	background-position: center center;
	position: relative;
	@include desktop {
		height: 100vh;
	}
	@include laptop {
		height: 100vh;
	}
	@include tablet {
		height: 100vh;
	}
}

.gallery__open {
	width: 100%;
	height: 266px;
	background: rgba(0, 0, 0, 0.68);
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 1rem 0;
	&.fadeout {
		opacity: 0;
		will-chanche: opacity;
	}
}

.camera-svg {
	width: rem(64);
	height: rem(48);
	fill: $primaryColor;
	margin-bottom: 0.7rem;
}

.gallery__open-link {
	display: block;
	min-width: rem(352);
	min-height: rem(37);
	font-size: rem(36);
	font-weight: normal;
	font-style: italic;
	line-height: 1rem;
	letter-spacing: 2.2px;
	text-align: center;
	color: $primaryColor;
	border-bottom: 1px solid $primaryColor;
	transition: color 0.2s, border-bottom 0.2s;
	&:hover, &:active {
		color: #edffc9;
		border-bottom: 1px solid #edffc9;
	}
}


